li {
  font-weight: normal;
  list-style: none;
  text-align: center;  
}
.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;
  grid-template-areas: 
  'h h'
  'n c';

  grid-template-rows: 1fr 30fr;
  grid-template-columns: 3fr 20fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: lightblue;
}

